import { __ } from '@wordpress/i18n';
import { insert } from '@wordpress/rich-text';
import { RichTextToolbarButton } from '@wordpress/block-editor';
import { Fragment, useState } from '@wordpress/element';
import IconModal from '../common/IconModal';

export default function Edit(props) {
    const { value, onChange } = props;
    const [isOpen, setOpen] = useState(false);

    const openModal = () => setOpen(true);
    const closeModal = () => setOpen(false);

    const insertIcon = (iconData, customOptions) => {
        const { customClass, customColor, customSize, customRotate, customCSS } = customOptions;

        let shortcode = `[${iconData["shortcode_txt"]} name="${iconData["name"]}"`;
        if (customClass) shortcode += ` class="${customClass}"`;
        if (customColor) shortcode += ` color="${customColor}"`;
        if (customSize) shortcode += ` size="${customSize}"`;
        if (customRotate) shortcode += ` rotate="${customRotate}"`;
        if (customCSS) shortcode += ` customcss="${customCSS}"`;
        shortcode += `]`;

        const newContent = insert(
            value,
            shortcode,
            value.start,
            value.end
        );

        onChange(newContent);
        closeModal();
    };

    return (
        <Fragment>
            <RichTextToolbarButton 
                icon="flag"
                title={ __( 'Manager for Icomoon (Shortcode)', 'managerforicomoon' ) }
                onClick={ openModal }
            />
            <IconModal
                isOpen={isOpen}
                closeModal={closeModal}
                insertIcon={insertIcon}
                title={ __( 'Manager for Icomoon (Shortcode version)', 'managerforicomoon' ) }
            />
        </Fragment>
    );
}