import { __ } from '@wordpress/i18n';
import { Fragment, useState, useEffect, useRef } from '@wordpress/element';
import { Modal, Button } from '@wordpress/components';

const IconModal = ({ isOpen, closeModal, insertIcon, title }) => {
    const [icons, setIcons] = useState([]);
    const [showOptions, setShowOptions] = useState(false);
    const colorPickerRef = useRef(null);
    const classRef = useRef(null);
    const colorRef = useRef(null);
    const sizeRef = useRef(null);
    const rotateRef = useRef(null);
    const cssRef = useRef(null);

    // Affichage/masquage des options
    const toggleOptions = () => {
        setShowOptions(!showOptions);
        setTimeout(() => {
            if (colorPickerRef.current) {
                jQuery(colorPickerRef.current).wpColorPicker();
            }
        }, 0);
    };

    useEffect(() => {
        if (isOpen) {
            fetch(managerforicomoon.ajaxurl, {
                method: 'POST',
                body: new URLSearchParams({
                    action: 'm4i_gutenberg_modal_insert_icon'
                }),
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded'
                }
            })
            .then(response => response.json())
            .then(response => {
                setIcons(Object.entries(response));
            })
            .catch(error => {
                console.error('Error fetching icons:', error);
            });

            if (colorPickerRef.current) {
                jQuery(colorPickerRef.current).wpColorPicker();
            }
        }
    }, [isOpen]);

    const handleInsertIcon = (iconData) => {
        const customClass = classRef.current ? classRef.current.value : '';
        const customColor = colorPickerRef.current ? colorPickerRef.current.value : '';
        const customSize = sizeRef.current ? sizeRef.current.value : '';
        const customRotate = rotateRef.current ? rotateRef.current.value : '';
        const customCSS = cssRef.current ? cssRef.current.value : '';

        insertIcon(iconData, {
            customClass,
            customColor,
            customSize,
            customRotate,
            customCSS
        });
    };

    return (
        <Fragment>
            {isOpen && (
                <Modal
                    title={title}
                    onRequestClose={closeModal}
                    className='managerforicomoon-modal--container'
                >
                    <div className='managerforicomoon-modal--content'>
                        <Button
                            onClick={toggleOptions}
                            className='managerforicomoon-modal--toggle-options'
                        >
                            { showOptions ? __( "Hide options", 'managerforicomoon' ) : __( "Show options", 'managerforicomoon' ) }
                        </Button>
                        {showOptions && (
                            <div className='managerforicomoon-modal--options'>
                                <h2>{ __( "Optional settings", 'managerforicomoon') }</h2>
                                <div className="managerforicomoon-modal--option">
                                    <label htmlFor="managerforicomoon-modal--option-class">{ __( "Custom class", 'managerforicomoon') }</label>
                                    <input type="text" name="m4i_custom_class" id="managerforicomoon-modal--option-class" ref={classRef} />
                                </div>
                                <div className="managerforicomoon-modal--option">
                                    <label htmlFor="managerforicomoon-modal--option-color">{ __( "Custom color", 'managerforicomoon') }</label>
                                    <input type="text" name="m4i_custom_color" id="managerforicomoon-modal--option-color" className="color-field" ref={colorPickerRef} />
                                </div>
                                <div className="managerforicomoon-modal--option">
                                    <label htmlFor="managerforicomoon-modal--option-size">{ __( "Custom size", 'managerforicomoon') }</label>
                                    <input type="text" name="m4i_custom_size" id="managerforicomoon-modal--option-size" ref={sizeRef} />
                                </div>
                                <div className="managerforicomoon-modal--option">
                                    <label htmlFor="managerforicomoon-modal--option-rotate">{ __( "Custom rotate", 'managerforicomoon') }</label>
                                    <input type="number" min="0" max="359" name="m4i_custom_rotate" id="managerforicomoon-modal--option-rotate" ref={rotateRef} />
                                </div>
                                <div className="managerforicomoon-modal--option managerforicomoon-modal--option-wide">
                                    <label htmlFor="managerforicomoon-modal--option-css">{ __( "Custom CSS", 'managerforicomoon') }</label>
                                    <input type="text" name="m4i_custom_css" id="managerforicomoon-modal--option-css" ref={cssRef} /> 
                                </div>
                            </div>
                        )}
                        {icons.length > 0 ? (
                            <div className='managerforicomoon-modal--icons-list'>
                                <h2>{ __( "Click to add an icon", 'managerforicomoon') }</h2>
                                {icons.map(([iconName, iconData], index) => (
                                    <Button 
                                        key={index} 
                                        onClick={() => handleInsertIcon(iconData)}
                                        className='managerforicomoon-modal--icon'
                                        title={iconData['name']}
                                    >
                                        {React.createElement(iconData['tag'], { className: iconData['class'] })}
                                    </Button>
                                ))}
                            </div>
                        ) : (
                            <p>{ __( 'Loading icons...', 'managerforicomoon' ) }</p>
                        )}
                    </div>
                </Modal>
            )}
        </Fragment>
    );
};

export default IconModal;
